import React from 'react';

import { TabsButton, Span } from './styled-components/styled-components';

const Tabs = props => {
  const { sortDescending, sortByAscending } = props;
  let color;
  const active = ({ target }) => {
    if (target.name === 'cheapest') {
      color = 'cheapest';
      return;
    }
    color = 'dearest';
  };

  console.log(color);

  return (
    <div className="tabs-wrapper">
      <TabsButton
        name="cheapest"
        onClick={e => {
          sortDescending();
          active(e);
        }}
        active="cheapest"
      >
        <Span>самый дешевый</Span>
      </TabsButton>
      <TabsButton
        name="dearest"
        onClick={e => {
          sortByAscending();
          active(e);
        }}
      >
        <Span>самый дорогой</Span>
      </TabsButton>
    </div>
  );
};

export default Tabs;
