export const transferFormatter = stops => {
  let postfix;
  if (stops.length === 1) {
    postfix = `${stops.length} пересадка`;
  }
  if (stops.length > 1) {
    postfix = `${stops.length} пересадки`;
  }
  if (stops.length === 0) {
    postfix = 'без пересадок';
  }
  return postfix;
};

export const arrivalTimes = (data, hours, min) => {
  let h = data.getHours() + hours;
  const allMinetes = data.getMinutes() + min;
  let minut = 0;
  if (h >= 24) {
    h -= 24;
    if (h >= 24) {
      h -= 24;
    }
  }
  if (allMinetes >= 60) {
    minut = allMinetes - 60;
    h += 1;
  } else {
    minut = allMinetes;
  }

  return `${h < 10 ? `0${h}` : h}:${minut < 10 ? `0${minut}` : minut}`;
};

export const sortingArray = (array, index = 'descending') => {
  if (index === 'descending') {
    return array.sort((a, b) => {
      if (a.price < b.price) return -1;
      if (a.price > b.price) return 1;
      return 0;
    });
  }
  if (index === 'ascending') {
    return array.sort((a, b) => {
      if (a.price > b.price) return -1;
      if (a.price < b.price) return 1;
      return 0;
    });
  }
  return false;
};
